import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);
delete require.cache['../views/index/index.vue'];

const router = new VueRouter({
  // mode: 'history',
  scrollBehavior: () => ({y: 0}),
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['../views/login.vue'], resolve)
    },
    {
      path: '/list',
      name: 'list',
      component: resolve => require(['../views/list.vue'], resolve),
      children: [{
          path: 'my',
          component: resolve => require(['../components/list.vue'], resolve),
        },
        {
          path: 'template',
          component: resolve => require(['../components/list.vue'], resolve),
        }
      ]
    },
    {
      path: '/statis/:sid',
      name: 'statis',
      component: resolve => require(['../views/statis.vue'], resolve),
    },
    {
      path: '/edit/:sid?',
      name: 'edit',
      component: resolve => require(['../views/edit.vue'], resolve),
      children: [{
          path: 'base',
          component: resolve => require(['../views/question/base.vue'], resolve),
        },
        {
          path: 'question',
          component: resolve => require(['../views/question/editQuestion.vue'], resolve),
          redirect: '/edit/:sid/question/questions/',
          children: [{
              path: 'questions',
              component: resolve => require(['../views/question/edit/questions.vue'], resolve),
            },
            {
              path: 'logic',
              component: resolve => require(['../views/question/edit/logic.vue'], resolve),
            },
            {
              path: 'appearance',
              component: resolve => require(['../views/question/edit/appearance.vue'], resolve),
            }
          ]
        }
      ]
    }
  ]
});

/**
 * 每次路由跳转是判断，是否解析了 微信配置需要参数
 * 若未获取则获取一次
 */

// router.beforeEach((to, from, next) => {
