//   let {openid,sceneId, state} = to.query;
//   next();
// });

// router.afterEach((to, from) => {
//   console.log(to, from, 'after', store);
//   if (Object.keys(from.query).length){
//     let {openid,sceneId} = from.query;
//     to.query = { openid, sceneId}
//   }
//
// })

export default router;



