import fetch from 'isomorphic-fetch';
import axios from 'axios';
import * as types from '../store/mutation-type';
import store from '../store/index';
import {
  MessageBox
} from 'element-ui';
import router from '../router/index';

/**
 * 封装fetch
 * @param path
 * @param params
 * @returns {*|Promise|Promise.<T>|Promise<U>}
 */
export default function toFetch(path, params, config) {
  let {
    //TODO:
    url = process.env.NODE_ENV == 'development' ? '' : process.env.NODE_ENV == 'pre' ? '//gapi.dev.ztgame.com' : '//gapi.ztgame.com',
    type = 'POST',
    contextType = 'application/json'
  } = config || {};
  // if (!url) {
  //   throw ('请求参数错误, url不能为空')
  // }
  url = `${url}${path}`;


  return axios(type === 'POST' ? {
    method: type,
    url,
    withCredentials: true,
    headers: {
      'Content-Type': contextType
    },
    data: params
  } : { method: type, url, withCredentials: true, })
    .then(res => {
      if (res.data.code != 1) {
        // 未登陆
        if (res.data.code == -9) {
          MessageBox.confirm('前往登录吗？', '登录状态已失效', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            router.push({
              path: `/login`
            });
          })
        } else if (res.data.errors) {
          let keys = Object.keys(res.data.errors)
          MessageBox.alert(res.data.errors[keys[0]][0], '提示', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }
      } else {
        return res.data;
      }
    })
    .catch(function (err) {
      // console.log(err);
      MessageBox.alert(err.message, '提示', {
        confirmButtonText: '确定',
        callback: action => {

        }
      });
      return {}
    })
}



