export function getParameterByName(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, '\\$&');
    var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
}

export function rem(size = 750) {
    let docEl = document.documentElement,
        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
        reCalc = () => {
            var clientWidth = docEl.clientWidth;
            if (!clientWidth) return;
            if (clientWidth >= size) {
                docEl.style.fontSize = '100px';
            } else {
                docEl.style.fontSize = 100 * (clientWidth / size) + 'px';
            }
        };

    if (!document.addEventListener) return;
    reCalc();
    window.addEventListener(resizeEvt, reCalc, false);
    document.addEventListener('DOMContentLoaded', reCalc, false);
}

let cacheMapImg = {

};
export function preLoadImg(imgs, callback) {
    let len = imgs.length;
    let i = 0;
    let loadOk = function (i, len, isError) {
        callback(parseInt(i / len * 100), isError);
    }

    imgs.forEach(item => {
        if (cacheMapImg[item]) {
            i++;
            loadOk(i, len);
        } else {
            let image = new Image();
            image.onload = function () {
                i++;
                loadOk(i, len);
            };
            image.onerror = function () {
                i++;
                loadOk(i, len, true);
            };
            image.src = item;
        }
    })
}
// sessionStorage操作
export function setStore(name, content) {
    if (!name) {
        return;
    }
    if (typeof content !== 'string') {
        content = JSON.stringify(content);
    }
    window.sessionStorage.setItem(name, content);
}
export function getStore(name) {
    let rtn = '';

    if (name) {
        rtn = window.sessionStorage.getItem(name);
    }
    return rtn;
}
export function removeStore(name) {
    if (!name) {
        return;
    }
    window.sessionStorage.removeItem(name);
}

export function isWeiXin() {
    //window.navigator.userAgent属性包含了浏览器类型、版本、操作系统类型、浏览器引擎类型等信息，这个属性可以用来判断浏览器类型
    var ua = window.navigator.userAgent.toLowerCase();
    //通过正则表达式匹配ua中是否含有MicroMessenger字符串
    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
        return true;
    } else {
        return false;
    }
}

export function isAndroid() {
    //window.navigator.userAgent属性包含了浏览器类型、版本、操作系统类型、浏览器引擎类型等信息，这个属性可以用来判断浏览器类型
    var ua = window.navigator.userAgent.toLowerCase();
    return ua.indexOf('android') > -1 || ua.indexOf('adr') > -1;
}

export function isMobile() {
    // if (/Wechat/.test(navigator.userAgent)) {
    //     return false;
    // }
    if (
        /AppleWebKit.*Mobile/i.test(navigator.userAgent) ||
        /Android|iPhone|Windows Phone|webOS|iPod|BlackBerry/i.test(navigator.userAgent)
    ) {
        return true;
    }
    return false;
}



