import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations';
import actions from './actions';
import getters from './getters';

import createLogger from 'vuex/dist/logger';


Vue.use(Vuex);

const debug = process.env.NODE_ENV == 'development';

/**
 * 输出 store
 */
export default new Vuex.Store({
  state: {
    loading: {
      show: false
    },
    modal: {
      show: false,
      guideName: 'group',
      word: '',  // 送的字
      msg: '',  // alert的主体msg
      commet: '',   // alert弹窗的括号内容
      btnName: '',  //按钮名字
      wordIndex: 0 // 摇一摇中了哪个字
    },
    selectedItem: {
      qIndex: null, // 选中的问题编号
      oIndex: null, // 选中的选项编号
    },
    questions: [],
    receive: false, // 登录后领字
    gold: 0, //绑金
    wish: '', //祝福语
    chance: 0, //游戏次数
    zone_name: '',  // 绑定的服务器名
    ga_account: '',  // 巨人账号
    wordList: null,  // 集字列表
    zoneList: null,  //服务器列表
    step: 'base' //默认路由
  },
  actions,
  mutations,
  getters,
  strict: debug,
  plugins: debug ? [createLogger()] : []
})




