import * as types from './mutation-type';
import Vue from 'vue';

export default {
  [types.SHOW_MODAL]: (state, {
    name,
    word,
    msg,
    commet,
    btnName,
    wordIndex,
    guideName
  }) => {
    state.modal = Object.assign({}, {
        show: true,
        name,
        word,
        msg,
        commet,
        btnName,
        wordIndex,
        guideName
      });
  },
  [types.HIDE_MODAL]: (state) => {
    state.modal = Object.assign({}, {
      show: false
    });
  },
  [types.SHOW_LOADING]: (state, percent) => {
    state.loading = Object.assign({}, {
      show: true,
      percent
    });
  },
  [types.HIDE_LOADING]: (state) => {
    state.loading = Object.assign({}, {
      show: false
    });
  },
  [types.SET_CHANCE]: (state, chance) => {
    state.chance = chance;
  },
  [types.UPDATE_CHANCE]: (state, delta) => {
    state.chance += delta;
  },
  [types.SET_ACCOUNT]: (state, account) => {
    state.ga_account = account;
  },
  [types.SET_ZONE]: (state, zone) => {
    state.zone_name = zone;
  },
  [types.SET_WORD_LIST]: (state, list) => {
    state.wordList = list;
  },
  [types.UPDATE_WORD_LIST]: (state, {id, number}) => {
    let index = state.wordList.findIndex(x => x.id == id);
    Vue.set(state.wordList, index, Object.assign({}, state.wordList[index], { number }));
  },
  [types.SET_ZONE_LIST]: (state, list) => {
    state.zoneList = list;
  },
  [types.SET_GOLD]: (state, gold) => {
    state.gold = gold;
  },
  [types.SET_WISH]: (state, wish) => {
    state.wish = wish;
  },
  [types.SET_RECEIVE]: (state, reveive) => {
    state.receive = reveive;
  },
  [types.SET_STEP]: (state, step) => {
    state.step = step;
  },
  [types.SET_SELECTED_ITEM]: (state, {
    qIndex,
    oIndex// 问题类型
  }) => {
    state.selectedItem = Object.assign({}, {
      qIndex,
      oIndex
    });
  },
  [types.CLEAR_SELECTED_ITEM]: (state) => {
    state.selectedItem = Object.assign({}, {
      qIndex: null, // 选中的问题编号
      oIndex: null// 问题类型
    });
  },
  [types.UPDATE_QUESTIONS]: (state, {index, data}) => {
    state.questions.splice(index, 1, data);
  },
  [types.ADD_QUESTIONS]: (state, data) => {
    state.questions.push(data);
    state.selectedItem = Object.assign({}, state.selectedItem, {
      qIndex: state.questions.length - 1, // 选中的问题编号
      oIndex: null // 问题类型
    });
  },
  [types.INSERT_QUESTIONS]: (state, {index, data}) => {
    state.questions.splice(index, 0, data);
  },
  [types.DELETE_QUESTIONS]: (state, index) => {
    state.questions.splice(index, 1);
  },
  [types.INIT_QUESTIONS]: (state, index) => {
    state.questions = [];
  },
  [types.SET_QUESTIONS]: (state, data) => {
    state.questions = null;
    state.questions = data;
  },
}



