import fetch from './toFetch';
import axios from 'axios';

let url = process.env.NODE_ENV == 'development' ? '' : process.env.NODE_ENV == 'pre' ? '//gapi.dev.ztgame.com' : '//gapi.ztgame.com';

export default {
  /**
   * 获取基础配置
   * @returns {*|Promise|Promise.<T>|Promise.<U>}
   */
  requestLogin(account, password) {
    return fetch('/surveyapi/b/admin/login', {
      account,
      password
    });
  },

  getTagList() {
    return fetch('/surveyapi/b/survey/tags', {}, {
      type: 'GET'
    });
  },

  getStatisInfo(sid) {
    return fetch(`/surveyapi/b/statis/view?sid=${sid}`, {}, {
      type: 'GET'
    });
  },

  logout() {
    return fetch('/surveyapi/b/admin/logout', {}, {
      type: 'GET'
    });
  },

  deleteSurvery(sid) {
    return fetch('/surveyapi/b/survey/del', {
      sid,
    });
  },

  copySurvery(sid) {
    return fetch('/surveyapi/b/survey/copy', {
      sid,
    });
  },

  /**
   * 获取基础配置
   * @returns {*|Promise|Promise.<T>|Promise.<U>}
   */
  upload(files) {
    let param = new FormData();
    // param.append("pic[]", files);
    for (let index = 0; index < files.length; index++) {
      const file = files[index];
      param.append("pic[]", file);
    }
    return axios({
      method: 'post',
      url: url + '/surveyapi/b/survey/upload',
      data: param,
      withCredentials: true,
      headers: {
        "Content-Type": "multipart/form-data"
      }
    }).then(res => {
        if (res.data.code != 1) {
          // 未登陆
          if (res.data.code == -9) {
            MessageBox.confirm('前往登录吗？', '登录状态已失效', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              router.push({
                path: `/login`
              });
            })
          } else if (res.errors && res.errors.error_message) {
            MessageBox.alert(res.errors.error_message[0], '提示', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          }
        } else {
          return res.data;
        }
      })
      .catch(function (err) {
        // console.log(err);
        MessageBox.alert(err.message, '提示', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
        return {}
      });
  },

  getList(page, pagesize=10, commissioned) {
    let baseUrl = `/surveyapi/b/survey/list?pagesize=${pagesize}&page=${page}`;
    if (commissioned) {
      baseUrl += `&commissioned=${commissioned}`;
    }
    return fetch(baseUrl, {}, {type: 'GET'});
  },

  getInfo(sid) {
    return fetch(`/surveyapi/b/survey/view?sid=${sid}`, {}, {
      type: 'GET'
    });
  },

  getInfo(sid) {
    return fetch(`/surveyapi/b/survey/view?sid=${sid}`, {}, {
      type: 'GET'
    });
  },

  exportData(sid) {
    return fetch(`/surveyapi/b/statis/export?sid=${sid}`, {}, {
      type: 'GET'
    });
  },

  getStatisDetail(sid, qid, page, pagesize) {
    return fetch(`/surveyapi/b/statis/details?sid=${sid}&qid=${qid}&page=${page}&pagesize=${pagesize}`, {}, {
      type: 'GET'
    });
  },

  addRole(sid, admin) {
    return fetch('/surveyapi/b/surveyadmin/add', {sid, admin});
  },

  deleteRole(sid, admin) {
    return fetch('/surveyapi/b/surveyadmin/del', {
      sid,
      admin
    });
  },

  getAdminList(sid) {
    return fetch(`/surveyapi/b/surveyadmin/list?sid=${sid}`, {}, {
      type: 'GET'
    });
  },

  sort(sid, sdata) {
    return fetch('/surveyapi/b/question/sort', { sid, sdata });
  },

  saveSurveyBase({
      title,
      desc,
      starttime,
      endtime,
      endmsg,
      status=0,
      logintype,
      tags,
      redirecturl,
      sid
    }) {
    let data = {
      title,
      desc,
      starttime,
      endtime,
      endmsg,
      status,
      tags,
      logintype,
      redirecturl
    };
    if(sid) {
      data.sid = sid;
    }
    return fetch('/surveyapi/b/survey/edit', data);
  },

  delelteQuestion(sid, qid) {
    return fetch('/surveyapi/b/question/del', {
      sid,
      qid
    });
  },

  copyQuestion(sid, qid) {
    return fetch('/surveyapi/b/question/copy', {
      sid,
      qid
    });
  },

  delelteLogic(id) {
    let param = new FormData();
    param.append("id", id);
    return fetch('/surveyapi/b/questionlogic/del', {
      id,
    });
  },

  saveQuestion({
    sid,
    qtype,
    sortid,
    subject,
    alength = 0,
    options,
    required = 1,
    qid,
    rows,
    unorder,
    rowsnum
  }) {
    let data = {
      sid,
      qtype,
      sortid,
      subject,
      alength,
      options,
      required
    };
    if (qid) {
      data.qid = qid;
    }
    if (rows) {
      data.rows = rows;
    }
    if (unorder) {
      data.unorder = unorder;
    }
    if (rowsnum) {
      data.rowsnum = rowsnum;
    }
    return fetch('/surveyapi/b/question/edit', data);
  },

  updateSurvery({sid, style, status, unorder}) {
    let data = { sid };
    if(style) {
      data.style = style;
    } else if (status) {
      data.status = status;
    } else if (unorder) {
      data.unorder = unorder;
    }
    return fetch('/surveyapi/b/survey/update', data);
  },

  getRuleList(sid) {
    return fetch(`/surveyapi/b/questionlogic/list?sid=${sid}`, {}, {type: 'GET'});
  },

  saveRule({
    sid,
    qid,
    qoid,
    target_qid,
    id
  }) {
    let data = {
      sid,
      qid,
      qoid,
      target_qid,
    };
    if (id) {
      data.id = id;
    }
    return fetch('/surveyapi/b/questionlogic/edit', data);
  }
}



